@import "https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400&family=Montserrat&family=Raleway:wght@400&display=swap";
*{margin: 0; padding: 0; box-sizing: border-box;}

.main{
    background-image: url("../images/bg.jpg");
    background-size:cover;
    background-repeat: no-repeat;
    height: 100vh;
    overflow: hidden;
}
.logo_cont{
    display: flex;
    flex-direction: row-reverse;
    
}
.logo{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items:center ;
    height: 74px;
    width: 74px;
    margin-right: 10px;
    margin-top: 20px;
    padding: 5px;
    mix-blend-mode: color-burn;
    
}
.lt{
    font-family: 'NATS';
    font-size: 13px;
    color: #DB4700;
}
.fc{
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translate(0px, 120px);
}
.fmo{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color:rgba(255, 255, 255, 70%);
    width: 271px;
    height: 265px;
    border-radius: 45px;
    padding-left: 20px;
    padding-right: 20px;

}
.form_outer{
    max-width: 300px;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
input{
    background-color:transparent;
    border: none;
    border-bottom: 1px solid #3c3c3c;
    padding: 5px 10px 5px 44px;

    height: 25px;
    width: 206px;
    font-size: 16px;
    display: block;
    width: 100%;
    outline: 0;
}
 ::placeholder{
font-family: 'Montserrat', sans-serif;
 font-size: 13px;
 color: rgba(71, 71, 71, 0.6);
} 

.form_input{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center; 
    width: 100%;
    margin: 10px 0;
}
.pcont{
    display: flex;
    justify-content: space-between;
    width: 100%;

}
.st{
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    font-size: 8px;
    color: #3c3c3c;
    line-height: 9.75px;
}
.p3{
    margin-top: 5px;
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    font-size: 9px;
    color: #3c3c3c;
    line-height: 9.75px;
}
p.p2{
    padding-left: 85px;
}
.logbtn {
    color: #fff;
    width: 178px;
    height: 39px;
    background-color: rgba(219, 71, 0, 1);
    border: none;
    border-radius: 15px;
    font-family: 'NATS';    
    margin-top: 40px;
    box-shadow: -3px -6px 4px rgba(239, 239, 239, 0.4), 3px 6px 4px rgba(196, 196, 196, 0.4);
    cursor: pointer;
}
span.stext{
    text-decoration:underline;
    cursor:pointer;
}


